  [dir="rtl"] .slick-next {
    left: -6rem;
    right: auto; }
  @media (max-width: 991px) {
    .slick-next {
      right: -2rem; } }

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 0; }

.slick-dots {
  position: absolute;
  display: block;
  margin: 0;
  padding: 0;
  bottom: 3rem;
  list-style: none;
  text-align: center; }
  .slick-dots li {
    display: inline-block;
    position: relative;
    height: 2rem;
    width: 2rem;
    margin: 0 3px;
    cursor: pointer; }
    .slick-dots li button {
      display: block;
      font-size: 0;
      border: 0;
      width: 2rem;
      height: 2rem;
      padding: 5px;
      outline: none;
      line-height: 0;
      cursor: pointer;
      color: transparent;
